.contact {
  padding: 6rem 0;
  
  @media screen and (max-width: 768px) {
    padding: 4rem 0;
  }
  
  @media screen and (max-width: 480px) {
    padding: 3rem 0;
  }
  
  &__header {
    text-align: center;
    margin-bottom: 4rem;
  }
  
  &__subtitle {
    font-size: clamp(1.1rem, 2vw, 1.3rem);
    color: var(--text-secondary);
    margin-top: 1rem;
    font-weight: 500;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
  }
  
  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 4rem;
    margin-bottom: 4rem;
    
    @media screen and (max-width: 968px) {
      grid-template-columns: 1fr;
      gap: 2rem;
      margin-bottom: 2rem;
    }
    
    @media screen and (max-width: 480px) {
      gap: 1.5rem;
    }
  }
  
  &__footer {
    margin-top: 2rem;
  }
}

.location-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border);
  border-radius: 16px;
  padding: 2.5rem;
  
  &__header {
    display: flex;
    align-items: flex-start;
    gap: 1rem;
    margin-bottom: 2rem;
    
    h3 {
      font-size: 1.5rem;
      font-weight: 700;
      color: var(--text-primary);
      margin-bottom: 0.5rem;
    }
    
    p {
      color: var(--text-secondary);
    }
  }
}

.location-icon {
  font-size: 2rem;
  padding: 0.75rem;
  background: var(--gradient-primary);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.availability {
  margin-bottom: 2rem;
  
  h4 {
    font-size: 1.1rem;
    font-weight: 600;
    color: var(--text-primary);
    margin-bottom: 1rem;
  }
  
  ul {
    list-style: none;
    
    li {
      display: flex;
      align-items: center;
      gap: 0.75rem;
      margin-bottom: 0.75rem;
      color: var(--text-secondary);
      
      .check-icon {
        color: var(--accent-color);
        font-weight: bold;
        font-size: 1.1rem;
      }
    }
  }
}

.contact__cta {
  p {
    color: var(--text-secondary);
    line-height: 1.6;
    font-style: italic;
  }
}

.contact-methods-grid {
  display: grid;
  gap: 1.5rem;
}

.contact-method {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border);
  border-radius: 16px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  padding: 2rem;
  display: flex;
  align-items: center;
  gap: 1.5rem;
  text-decoration: none;
  color: inherit;
  position: relative;
  
  &:hover {
    transform: translateY(-8px);
    box-shadow: var(--shadow-xl);
  }
  
  @media screen and (max-width: 768px) {
    padding: 1.5rem;
    gap: 1rem;
  }
  
  @media screen and (max-width: 480px) {
    padding: 1rem;
    flex-direction: column;
    text-align: center;
    gap: 1rem;
    
    &:hover {
      transform: translateY(-4px);
    }
  }
  
  &__icon {
    font-size: 2rem;
    padding: 1rem;
    background: var(--gradient-primary);
    border-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
  }
  
  &__content {
    flex: 1;
    
    h4 {
      font-size: 1.1rem;
      font-weight: 600;
      color: var(--text-primary);
      margin-bottom: 0.25rem;
    }
  }
  
  &__value {
    color: var(--accent-color);
    font-weight: 600;
    margin-bottom: 0.25rem;
  }
  
  &__description {
    color: var(--text-muted);
    font-size: 0.9rem;
  }
  
  &__arrow {
    color: var(--accent-color);
    opacity: 0;
    transform: translateX(-10px);
    transition: all 0.3s ease;
  }
  
  &:hover {
    .contact-method__arrow {
      opacity: 1;
      transform: translateX(0);
    }
  }
}

.contact-footer-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border);
  border-radius: 16px;
  padding: 3rem;
  text-align: center;
  
  h3 {
    font-size: 1.5rem;
    font-weight: 700;
    color: var(--text-primary);
    margin-bottom: 1rem;
  }
  
  p {
    color: var(--text-secondary);
    line-height: 1.6;
    margin-bottom: 2rem;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
  }
}

.contact-footer-actions {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  flex-wrap: wrap;
}
  