.portfolio {
  padding: 6rem 0;
  
  @media screen and (max-width: 768px) {
    padding: 4rem 0;
  }
  
  @media screen and (max-width: 480px) {
    padding: 3rem 0;
  }
  
  &__header {
    text-align: center;
    margin-bottom: 3rem;
  }
  
  &__subtitle {
    font-size: clamp(1.1rem, 2vw, 1.3rem);
    color: var(--text-secondary);
    margin-top: 1rem;
    font-weight: 500;
  }
  
  &__filters {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 1rem;
    margin-bottom: 4rem;
    
    @media screen and (max-width: 768px) {
      gap: 0.5rem;
      margin-bottom: 2rem;
    }
    
    @media screen and (max-width: 480px) {
      gap: 0.25rem;
    }
  }
  
  &__grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 2rem;
    
    @media screen and (max-width: 768px) {
      grid-template-columns: 1fr;
      gap: 1.5rem;
    }
    
    @media screen and (max-width: 480px) {
      gap: 1rem;
    }
  }
}

.filter-btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 50px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border);
  color: var(--text-secondary);
  font-weight: 600;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  
  @media screen and (max-width: 480px) {
    padding: 0.5rem 1rem;
    font-size: 0.8rem;
  }
  
  &:hover {
    background: var(--surface-light);
    color: var(--text-primary);
    transform: translateY(-2px);
  }
  
  &--active {
    background: var(--gradient-primary);
    color: var(--primary-color);
    transform: translateY(-2px);
    box-shadow: var(--shadow-md);
  }
}

.project-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border);
  border-radius: 16px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  overflow: hidden;
  
  &:hover {
    transform: translateY(-8px);
    box-shadow: var(--shadow-xl);
  }
  
  @media screen and (max-width: 480px) {
    &:hover {
      transform: translateY(-4px);
    }
  }
  
  &__image {
    position: relative;
    height: 200px;
    overflow: hidden;
    border-radius: 12px;
    margin-bottom: 1.5rem;
    
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: transform 0.3s ease;
    }
    
    &:hover img {
      transform: scale(1.05);
    }
  }
  
  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(15, 15, 35, 0.8);
    backdrop-filter: blur(4px);
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.3s ease;
    
    .project-card__image:hover & {
      opacity: 1;
    }
  }
  
  &__actions {
    display: flex;
    gap: 1rem;
  }
  
  &__content {
    padding: 1.5rem;
    
    @media screen and (max-width: 480px) {
      padding: 1rem;
    }
  }
  
  &__tags {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin-bottom: 1rem;
  }
  
  &__title {
    font-size: 1.25rem;
    font-weight: 700;
    color: var(--text-primary);
    margin-bottom: 0.75rem;
    line-height: 1.3;
  }
  
  &__description {
    color: var(--text-secondary);
    line-height: 1.6;
    margin-bottom: 1.5rem;
  }
  
  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.project-tag {
  padding: 0.25rem 0.75rem;
  background: rgba(0, 212, 255, 0.1);
  color: var(--accent-color);
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 500;
  border: 1px solid rgba(0, 212, 255, 0.2);
}

.action-btn {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: var(--gradient-primary);
  color: var(--primary-color);
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  transition: all 0.3s ease;
  
  svg {
    width: 20px;
    height: 20px;
  }
  
  &:hover {
    transform: scale(1.1);
    box-shadow: var(--shadow-md);
  }
}

.project-link {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--accent-color);
  text-decoration: none;
  font-weight: 600;
  font-size: 0.9rem;
  transition: all 0.3s ease;
  
  svg {
    width: 16px;
    height: 16px;
    transition: transform 0.3s ease;
  }
  
  &:hover {
    color: var(--text-primary);
    
    svg {
      transform: translate(2px, -2px);
    }
  }
}
  