.about {
  padding: 6rem 0;
  
  @media screen and (max-width: 768px) {
    padding: 4rem 0;
  }
  
  @media screen and (max-width: 480px) {
    padding: 3rem 0;
  }
  
  &__header {
    text-align: center;
    margin-bottom: 4rem;
  }
  
  &__subtitle {
    font-size: clamp(1.1rem, 2vw, 1.3rem);
    color: var(--text-secondary);
    margin-top: 1rem;
    font-weight: 500;
  }
  
  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 4rem;
    margin-bottom: 4rem;
    
    @media screen and (max-width: 968px) {
      grid-template-columns: 1fr;
      gap: 2rem;
      margin-bottom: 2rem;
    }
    
    @media screen and (max-width: 480px) {
      gap: 1.5rem;
    }
  }
  
  &__stats {
    margin-top: 2rem;
  }
}

.story-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border);
  border-radius: 16px;
  padding: 2.5rem;
  height: 100%;
  
  @media screen and (max-width: 768px) {
    padding: 2rem;
  }
  
  @media screen and (max-width: 480px) {
    padding: 1.5rem;
  }
  
  &__header {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: 1.5rem;
    
    h3 {
      font-size: 1.5rem;
      font-weight: 700;
      color: var(--text-primary);
    }
  }
  
  &__icon {
    font-size: 2rem;
    padding: 0.75rem;
    background: var(--gradient-primary);
    border-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  p {
    margin-bottom: 1.5rem;
    line-height: 1.7;
    color: var(--text-secondary);
    
    strong {
      color: var(--accent-color);
      font-weight: 600;
    }
  }
  
  &__highlight {
    margin-top: 2rem;
    padding: 1.5rem;
    background: rgba(0, 212, 255, 0.1);
    border-left: 4px solid var(--accent-color);
    border-radius: 8px;
    
    .highlight-text {
      font-style: italic;
      color: var(--accent-color);
      font-weight: 500;
    }
  }
}

.skills {
  &__title {
    font-size: 1.5rem;
    font-weight: 700;
    color: var(--text-primary);
    margin-bottom: 2rem;
  }
  
  &__grid {
    display: grid;
    gap: 1.5rem;
  }
}

.skill-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border);
  border-radius: 16px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  
  &:hover {
    transform: translateY(-8px);
    box-shadow: var(--shadow-xl);
  }
  padding: 1.5rem;
  
  &__header {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    margin-bottom: 1rem;
    
    h4 {
      font-size: 1rem;
      font-weight: 600;
      color: var(--text-primary);
    }
  }
  
  &__icon {
    font-size: 1.5rem;
  }
  
  &__progress {
    display: flex;
    align-items: center;
    gap: 1rem;
  }
}

.progress-bar {
  flex: 1;
  height: 8px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  overflow: hidden;
  
  &__fill {
    height: 100%;
    background: var(--gradient-primary);
    border-radius: 4px;
    position: relative;
    
    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
      animation: shimmer 2s infinite;
    }
  }
}

.skill-level {
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--accent-color);
  font-family: var(--font-mono);
  min-width: 40px;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2rem;
}

.stat-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border);
  border-radius: 16px;
  padding: 2rem;
  text-align: center;
  
  &__number {
    font-size: clamp(2rem, 4vw, 3rem);
    font-weight: 900;
    background: var(--gradient-primary);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    margin-bottom: 0.5rem;
    font-family: var(--font-mono);
  }
  
  &__label {
    font-size: 1rem;
    color: var(--text-secondary);
    font-weight: 500;
  }
}

@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

