.experiences {
  padding: 6rem 0;
  
  @media screen and (max-width: 768px) {
    padding: 4rem 0;
  }
  
  @media screen and (max-width: 480px) {
    padding: 3rem 0;
  }
  
  &__header {
    text-align: center;
    margin-bottom: 4rem;
  }
  
  &__subtitle {
    font-size: clamp(1.1rem, 2vw, 1.3rem);
    color: var(--text-secondary);
    margin-top: 1rem;
    font-weight: 500;
  }
  
  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 4rem;
    
    @media screen and (max-width: 1200px) {
      grid-template-columns: 1fr;
      gap: 2rem;
    }
    
    @media screen and (max-width: 480px) {
      gap: 1.5rem;
    }
  }
}

.section-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 2rem;
  text-align: center;
}

.testimonial-container {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border);
  border-radius: 16px;
  padding: 2.5rem;
  text-align: center;
  
  @media screen and (max-width: 768px) {
    padding: 2rem;
  }
  
  @media screen and (max-width: 480px) {
    padding: 1.5rem;
  }
}

.testimonial-content {
  margin-bottom: 2rem;
  
  .quote-icon {
    font-size: 4rem;
    color: var(--accent-color);
    opacity: 0.3;
    margin-bottom: 1rem;
    font-family: serif;
  }
}

.testimonial-text {
  font-size: 1.1rem;
  line-height: 1.7;
  color: var(--text-secondary);
  font-style: italic;
  margin-bottom: 2rem;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}

.testimonial-author {
  h4 {
    font-size: 1.1rem;
    font-weight: 600;
    color: var(--text-primary);
    margin-bottom: 0.25rem;
  }
  
  span {
    color: var(--accent-color);
    font-size: 0.9rem;
    font-weight: 500;
  }
}

.testimonial-controls {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}

.control-btn {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: none;
  background: var(--surface-light);
  color: var(--text-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  
  svg {
    width: 20px;
    height: 20px;
  }
  
  &:hover {
    background: var(--accent-color);
    color: var(--primary-color);
    transform: scale(1.1);
  }
}

.testimonial-indicators {
  display: flex;
  gap: 0.5rem;
  
  .indicator {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    border: none;
    background: var(--surface-light);
    cursor: pointer;
    transition: all 0.3s ease;
    
    &.active {
      background: var(--accent-color);
      transform: scale(1.2);
    }
  }
}

.app__experiences-item {
  flex-direction: column;
  text-align: center;

  margin: 1rem;

  transition: all 0.3s ease-in-out;

  div {
    width: 90px;
    height: 90px;
    border-radius: 50%;
    background-color: #fef4f5;

    img {
      width: 50%;
      height: 50%;
    }

    &:hover {
      box-shadow: 0 0 25px #fef4f5;
    }

    @media screen and (min-width: 2000px) {
      width: 150px;
      height: 150px;
    }

    @media screen and (max-width: 450px) {
      width: 70px;
      height: 70px;
    }
  }

  p {
    font-weight: 500;
    margin-top: 0.5rem;
  }

  @media screen and (min-width: 2000px) {
    margin: 1rem 2rem;

    p {
      margin-top: 1rem;
    }
  }
}

.app__experiences-exp {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  padding: 0 2rem;
  @media screen and (max-width: 900px) {
    margin-top: 2rem;
  }
}

.timeline {
  position: relative;
  
  &::before {
    content: '';
    position: absolute;
    left: 50%;
    top: 0;
    bottom: 0;
    width: 2px;
    background: var(--gradient-primary);
    transform: translateX(-50%);
    
    @media screen and (max-width: 768px) {
      left: 20px;
    }
  }
}

.timeline-item {
  position: relative;
  margin-bottom: 3rem;
  
  &--left {
    .timeline-content {
      margin-right: calc(50% + 2rem);
      
      @media screen and (max-width: 768px) {
        margin-right: 0;
        margin-left: 3rem;
      }
    }
    
    .timeline-marker {
      right: calc(50% - 12px);
      
      @media screen and (max-width: 768px) {
        left: 8px;
        right: auto;
      }
    }
  }
  
  &--right {
    .timeline-content {
      margin-left: calc(50% + 2rem);
      
      @media screen and (max-width: 768px) {
        margin-left: 3rem;
        margin-right: 0;
      }
    }
    
    .timeline-marker {
      left: calc(50% - 12px);
      
      @media screen and (max-width: 768px) {
        left: 8px;
      }
    }
  }
}

.timeline-content {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border);
  border-radius: 16px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  padding: 2rem;
  position: relative;
  
  &:hover {
    transform: translateY(-8px);
    box-shadow: var(--shadow-xl);
  }
  
  @media screen and (max-width: 768px) {
    padding: 1.5rem;
    
    &:hover {
      transform: translateY(-4px);
    }
  }
  
  @media screen and (max-width: 480px) {
    padding: 1rem;
  }
  
  &::before {
    content: '';
    position: absolute;
    top: 2rem;
    width: 0;
    height: 0;
    border: 10px solid transparent;
    
    .timeline-item--left & {
      right: -20px;
      border-left-color: rgba(255, 255, 255, 0.1);
      
      @media screen and (max-width: 768px) {
        left: -20px;
        right: auto;
        border-right-color: rgba(255, 255, 255, 0.1);
        border-left-color: transparent;
      }
    }
    
    .timeline-item--right & {
      left: -20px;
      border-right-color: rgba(255, 255, 255, 0.1);
      
      @media screen and (max-width: 768px) {
        border-right-color: rgba(255, 255, 255, 0.1);
      }
    }
  }
}

.timeline-header {
  margin-bottom: 1.5rem;
}

.timeline-period {
  display: inline-block;
  padding: 0.5rem 1rem;
  background: var(--gradient-primary);
  color: var(--primary-color);
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 600;
  margin-bottom: 1rem;
  font-family: var(--font-mono);
}

.timeline-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.timeline-company {
  font-size: 1rem;
  color: var(--accent-color);
  font-weight: 600;
  margin-bottom: 1rem;
}

.timeline-description {
  p {
    color: var(--text-secondary);
    line-height: 1.6;
    margin-bottom: 0.75rem;
    
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.timeline-marker {
  position: absolute;
  top: 2rem;
  transform: translateY(-50%);
}

.timeline-dot {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: var(--gradient-primary);
  border: 4px solid var(--primary-color);
  box-shadow: var(--shadow-md);
}

.experiences-tooltip {
  max-width: 300px !important;
  background-color: var(--white-color) !important;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.1) !important;
  border-radius: 5px !important;
  padding: 1rem !important;
  color: var(--gray-color) !important;
  text-align: center !important;
  line-height: 1.5 !important;
  opacity: 1 !important;

  @media screen and (min-width: 2000px) {
    font-size: 1.75rem !important;
    max-width: 500px !important;
    line-height: 2 !important;
  }
}

.italic-color {
  color: var(--gray-color) !important;
}
