.floating-elements {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
  overflow: hidden;
}

.floating-element {
  position: absolute;
  border-radius: 50%;
  background: linear-gradient(135deg, rgba(99, 102, 241, 0.1), rgba(236, 72, 153, 0.1));
  backdrop-filter: blur(2px);
  
  &:nth-child(1) {
    top: 10%;
    left: 10%;
  }
  
  &:nth-child(2) {
    top: 20%;
    right: 15%;
  }
  
  &:nth-child(3) {
    bottom: 30%;
    left: 20%;
  }
  
  &:nth-child(4) {
    bottom: 20%;
    right: 10%;
  }
  
  &:nth-child(5) {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}