#home {
  .app__wrapper {
    padding: 0;
  }
}

.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
  width: 100%;
  
  &__container {
    max-width: 1400px;
    margin: 0 auto;
    padding: 1rem;
    width: 100%;
    
    @media screen and (min-width: 768px) {
      padding: 2rem;
    }
  }
  
  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 4rem;
    align-items: center;
    min-height: 80vh;
    
    @media screen and (max-width: 968px) {
      grid-template-columns: 1fr;
      gap: 2rem;
      text-align: center;
      min-height: auto;
      padding: 2rem 0;
    }
    
    @media screen and (max-width: 480px) {
      gap: 1.5rem;
      padding: 1rem 0;
    }
  }
  
  &__intro {
    z-index: 2;
  }
  
  &__greeting {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    margin-bottom: 1rem;
    
    p {
      font-size: clamp(1.1rem, 2vw, 1.3rem);
      color: var(--text-secondary);
      font-weight: 500;
    }
  }
  
  &__wave {
    font-size: 2rem;
    animation: wave 2s ease-in-out infinite;
  }
  
  &__name {
    font-size: clamp(3rem, 8vw, 5rem);
    font-weight: 900;
    line-height: 1;
    margin-bottom: 1.5rem;
    background: linear-gradient(135deg, var(--text-primary) 0%, var(--accent-color) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  
  &__title {
    height: 3rem;
    margin-bottom: 2rem;
    display: flex;
    align-items: center;
    
    @media screen and (max-width: 968px) {
      justify-content: center;
    }
  }
  
  &__description {
    font-size: clamp(1rem, 2vw, 1.2rem);
    line-height: 1.7;
    color: var(--text-secondary);
    margin-bottom: 3rem;
    max-width: 600px;
    
    .highlight {
      color: var(--accent-color);
      font-weight: 600;
    }
  }
  
  &__cta {
    display: flex;
    gap: 1.5rem;
    flex-wrap: wrap;
    
    @media screen and (max-width: 968px) {
      justify-content: center;
    }
    
    @media screen and (max-width: 480px) {
      gap: 1rem;
      flex-direction: column;
      align-items: center;
    }
  }
  
  &__visual {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  
  &__image-container {
    position: relative;
    width: 400px;
    height: 400px;
    
    @media screen and (max-width: 968px) {
      width: 280px;
      height: 280px;
    }
    
    @media screen and (max-width: 480px) {
      width: 240px;
      height: 240px;
    }
  }
  
  &__image-bg {
    position: absolute;
    top: -20px;
    left: -20px;
    right: -20px;
    bottom: -20px;
    background: var(--gradient-primary);
    border-radius: 50%;
    opacity: 0.1;
    animation: pulse 3s ease-in-out infinite;
  }
  
  &__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
    border: 4px solid var(--accent-color);
    position: relative;
    z-index: 2;
  }
  
  &__floating-elements {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    pointer-events: none;
  }
  
  &__scroll-indicator {
    position: absolute;
    bottom: 2rem;
    left: 50%;
    transform: translateX(-50%);
    
    @media screen and (max-width: 968px) {
      position: relative;
      margin-top: 3rem;
    }
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem 2rem;
  border-radius: 50px;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 2px solid transparent;
  font-size: 1rem;
  
  @media screen and (max-width: 480px) {
    padding: 0.8rem 1.5rem;
    font-size: 0.9rem;
  }
  
  &--primary {
    background: var(--gradient-primary);
    color: var(--primary-color);
    
    &:hover {
      transform: translateY(-2px);
      box-shadow: var(--shadow-lg);
    }
  }
  
  &--secondary {
    background: transparent;
    color: var(--text-primary);
    border-color: var(--border);
    
    &:hover {
      background: var(--surface-light);
      border-color: var(--accent-color);
      transform: translateY(-2px);
    }
  }
}

.floating-element {
  position: absolute;
  padding: 0.5rem 1rem;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border);
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  color: var(--accent-color);
  font-family: var(--font-mono);
  
  @media screen and (max-width: 768px) {
    display: none;
  }
  
  &--1 {
    top: 10%;
    right: 10%;
    animation: float 6s ease-in-out infinite;
  }
  
  &--2 {
    top: 60%;
    right: -10%;
    animation: float 6s ease-in-out infinite 1s;
  }
  
  &--3 {
    bottom: 20%;
    left: -10%;
    animation: float 6s ease-in-out infinite 2s;
  }
  
  &--4 {
    top: 30%;
    left: 10%;
    animation: float 6s ease-in-out infinite 3s;
  }
}

.scroll-indicator {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  color: var(--text-muted);
  font-size: 0.9rem;
  
  span {
    font-weight: 500;
  }
  
  &__line {
    width: 2px;
    height: 40px;
    background: var(--gradient-primary);
    border-radius: 2px;
    position: relative;
    
    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 20px;
      background: var(--accent-color);
      border-radius: 2px;
      animation: scroll-indicator 2s ease-in-out infinite;
    }
  }
}

@keyframes wave {
  0%, 100% { transform: rotate(0deg); }
  25% { transform: rotate(20deg); }
  75% { transform: rotate(-10deg); }
}

@keyframes pulse {
  0%, 100% { transform: scale(1); opacity: 0.1; }
  50% { transform: scale(1.05); opacity: 0.2; }
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-20px); }
}

@keyframes scroll-indicator {
  0% { transform: translateY(0); opacity: 1; }
  100% { transform: translateY(20px); opacity: 0; }
}
